import React, { Component } from 'react'
import { Table ,message} from 'antd'
//引入自定义组件
import AdvancedSearchForm from '../../components/AdvancedSearchForm'
//引入工具
import {ConvertFomrData} from '../../utils'
//引入API
import { getV_CgInfo} from '../../api'
//配置 
import { V_CgInfo_columns } from '../../config/table-columns'

import { V_CgInfoFielsds } from '../../config/form-Fields'

export default class Report4 extends Component {
    state = {
        V_CgInfo_columns:[],
        dataSource: [],
        dataTotal: 0,
        loading: false,
    }
    //搜索数据
    SearchData = async (formData) => {
        if (formData === undefined) {
            formData = new FormData();
        } else {
            formData = ConvertFomrData(formData);
        }
        this.setState({ loading: true });
        const result = await getV_CgInfo(formData);
        if(result.status === 0){
            const { V_CgInfo } = result.data;
            this.setState({ dataSource: V_CgInfo,loading: false,V_CgInfo_columns:V_CgInfo_columns})
        }else{
            this.setState({loading: false})
            message.error("服务器无响应");
        }
    }
    componentDidMount = () => {
        this.SearchData();
    }
    render() {
        const { dataSource, loading,V_CgInfo_columns} = this.state;
        return (
            <div className="main">
                <div>
                    <AdvancedSearchForm SearchData={this.SearchData} Fielsds={V_CgInfoFielsds} form={this.form} />
                </div>
                <Table
                dataSource={dataSource}
                bordered
                rowKey="ID"
                sticky={true}
                scroll={{ x: 1700}} 
                columns={V_CgInfo_columns}
                size="middle"
                loading={loading}
                pagination={false}
                >
                </Table>
            </div>
        )
    }
}
