import React, { Component } from 'react'
import { Table,message} from 'antd'
//引入自定义组件
import AdvancedSearchForm from '../../components/AdvancedSearchForm'
//引入工具
import {ConvertFomrData} from '../../utils'
//引入API
import { getV_JjInfo} from '../../api'
//配置 
import { V_JjInfo_columns } from '../../config/table-columns'
import { V_JjInfoFielsds } from '../../config/form-Fields'

export default class Report3 extends Component {
    state = {
        dataSource: [],
        dataTotal: 0,
        loading: true, 
        V_JjInfo_columns:[],
    }
    //搜索数据
    SearchData = async (formData) => {
        if (formData === undefined) {
            formData = new FormData();
        } else {
            formData = ConvertFomrData(formData);
        }
        this.setState({ loading: true });
        const result = await getV_JjInfo(formData);
        if(result.status === 0){
            const { V_JjInfo } = result.data;
            this.setState({ dataSource: V_JjInfo,loading: false,V_JjInfo_columns:V_JjInfo_columns})
        }else{
            this.setState({loading: false,V_JjInfo_columns})
            message.error("服务器无响应");
        } 
    }
    componentDidMount = () => {
        this.SearchData();
    }
    render() {
        const { dataSource, loading,V_JjInfo_columns} = this.state;
        return (
            <div className="main">
                <div>
                    <AdvancedSearchForm SearchData={this.SearchData} Fielsds={V_JjInfoFielsds} form={this.form} />
                </div>
                <Table
                dataSource={dataSource}
                bordered
                rowKey="ID"
                sticky={true}
                //scroll={{ x: 2600}} 
                columns={V_JjInfo_columns}
                size="middle"
                loading={loading}
                pagination={false}
                >
                </Table>
            </div>
        )
    }
}
