import React, { Component } from 'react'
import { Table,message} from 'antd'
//引入工具
import {ConvertFomrData} from '../../utils'
//引入API 
import {getV_BjInfo} from '../../api'
//配置 
import { V_BjInfo_columns } from '../../config/table-columns'
//模拟数据
// import { BjInfo } from '../../anaData'
export default class Report2 extends Component {
    state = { 
        dataSource: [],
        V_BjInfo_columns:[],
        dataTotal: 0,
        loading: true,
    }
    //搜索数据
    SearchData = async (formData) => {
        if(formData===undefined){
            formData = new FormData();
        }else{
            formData = ConvertFomrData(formData);
        }
        this.setState({loading:true})
        const result = await getV_BjInfo(formData);
        if(result.status===0){
            const {V_BjInfo} = result.data;
            this.setState({ dataSource: V_BjInfo,loading:false,V_BjInfo_columns:V_BjInfo_columns})
        }else{
            this.setState({loading: false,V_BjInfo_columns})
            message.error("服务器无响应");
        }
    }
    componentDidMount = () => {
        this.SearchData();
    }
    render() {
        const { dataSource, loading,V_BjInfo_columns} = this.state;
        return (
            <div className="main">
                <Table
                dataSource={dataSource}
                bordered
                rowKey="ID"
                sticky={true}
                scroll={{ x: 2600}} 
                columns={V_BjInfo_columns}
                size="middle"
                loading={loading}
                pagination={false}
                >
                </Table>
            </div>
        )
    }
}
